@tailwind base;
@tailwind components;
@tailwind utilities;
::-webkit-scrollbar {

    width: 5px;
  
    height: 5px;
  
    border-radius: 10px;
  
  }
  
  ::-webkit-scrollbar-track {
  
      background-color: #efefef;
    
      border-radius: 10px;
    
    }
    
    
    
    ::-webkit-scrollbar-thumb {
    
      background-color: #ccc;
    
      border-radius: 10px;
    
    }
    
    
    
    ::-webkit-scrollbar-button {
    
      display: none;
    
    }
